.card__container {
    width: 410px;
    height: 435px;
    border-radius: 18px;
    background-color: #fff;
    position: relative;
}

.bg__card--top {
    width: 100%;
    border-radius: 18px 10px 0 0;
}

.user__img {
    width: 113px;
    position: absolute;
    right: 148px;
    top: 114px;
    border-radius: 50%;
    border: 5px solid #fff;
}

.user__info {
    margin-top: 4rem;
}

.user__name {
    font-size: 23px;
    font-weight: 700;
}

.age {
    color: hsl(229, 23%, 23%);
    font-weight: 400;
}

.country {
    margin-top: .6rem;
    color: hsl(229, 23%, 23%);
    margin-bottom: 3.3rem;
}

.user__stats {
   border-top: .9px solid  hsl(225, 19%, 84%);
   display: flex;
   padding: 1.3rem;
}

.user__stats div {
    margin: 0 1.9rem 0 1.9rem;
}

.stat {
    font-weight: 700;
    font-family: 'Kumbh Sans', sans-serif;
    margin-bottom: 1rem;
    letter-spacing: 1px;
}

.stat--name {
    font-weight: 400;
    font-size: 17px;
    color:hsl(227, 10%, 46%);
}

@media (max-width: 375px) {
    .card__container {
        margin-top: 5rem;
        width: 340px;
        height: 400px;
    }

    .user__img {
        right: 112px;
        top: 82px;
    }

    .user__stats div {
        margin: 0 1.3rem 0 1.3rem;
    }

}