* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --dark-cyan-bg: hsl(185, 75%, 39%);
}

body {
  font-family: 'Kumbh Sans', sans-serif;
  font-size: 18px;
  background-color: var(--dark-cyan-bg);
  background-image: url("img/bg-pattern-top.svg"), url("img/bg-pattern-bottom.svg");
  background-position: left -400px top -580px, right -500px bottom -600px;
  background-repeat: no-repeat, no-repeat;
  padding: 5rem;
}

.App {
  display: grid;
  place-items: center;
  text-align: center;
}

.attribution {
  margin-top: 1.1rem;
  font-size: 16px;
  transition: .3s all;
}

.attribution a:hover {
  text-decoration: none;
  color: #000000;
}

@media (max-width: 375px) {
  body {
    padding: 0;
  }

  .attribution {
    font-size: 13px;
  }
}